.wrap{
    height: 100%;
    overflow: scroll;
}

.wrap::-webkit-scrollbar {
    width: 0;
}

.title{
    margin-bottom: 15px;
    font-size: 28px;
}

.mainWrap{
    padding: 20px;
}

.userInfoWrap{
    margin-bottom: 15px;
    border-bottom: 2px solid #414141;
    width: 250px;
    padding-bottom: 3px;
    display: flex;
}

.userTitleWRap{
    display: flex;
    margin-bottom: 25px;
    align-items: center;
    width: 500px;
    justify-content: space-between;
    border-bottom: 1px solid  #414141;
}

.hidden{
    display: none;
}

.usual{
    margin-top: 20px;
    height: 60px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}



