.root{
    width: 100%;
    height: 100%;
    background: url('./images/bacground-body.jpg') center no-repeat fixed;

}

.app-wrapper {
    width: 80%;
    height: 100%;
    margin: 0 auto;
    display: grid;
    grid-template-rows: 2fr 18fr;
    grid-template-columns: 2fr 10fr;
    grid-template-areas:
        "h h"
        "n c";
    position: relative;
}

.content_wrapper {
    grid-area: c;
    overflow: hidden;
    color: aliceblue;
    border-left: 1px solid #414141;
    border-right: 1px solid #414141;
}

.content_wrapper::-webkit-scrollbar {
    width: 0;
}

.content_wrapper{
    scrollbar-width: none;
}

