.wrap input{
    background-color: #c4c4c4;
    border-radius: 12px;
    width: 60%;
    height: 45px;
    border: none;
    outline: none;
    margin-right: 20px;
}

.wrap{
    display: flex;
    margin-top: 50px;
    width: 100%;
    align-items: center;
    justify-content: center;
}

.wrap button{
    height: 45px;
    width: 60px;
    border-radius: 10px;
}